<template>
  <div>
    <TemplatePlayGround
        :header-nav="headerNav"
        :template-name="sectionName"
        :template-title="sectionTitle"
        :template-description="sectionDescription">
    </TemplatePlayGround>

    <!-- region Main Content -->
    <section class="blog">
      <div class="container align-items-center">

        <!-- Coordinates & Chart -->
        <div class="row">

          <div class="col align-self-center">
            <!-- Coordinates -->
            <div>
              <h5>Coordinates</h5>

              <div class="form-group row">
                <label class="col-sm-3 col-form-label">X</label>
                <div class="col-sm-8">
                  <input class="form-control" @change="refreshChart" v-model="x">
                </div>
                <label class="col-sm-1 col-form-label">{{ lengthOfX }}</label>
              </div>

              <div class="form-group row">
                <label class="col-sm-3 col-form-label">Y</label>
                <div class="col-sm-8">
                  <input class="form-control" @change="refreshChart" v-model="y">
                </div>
                <label class="col-sm-1 col-form-label">{{ lengthOfY }}</label>
              </div>
            </div>
          </div>
          <div class="col">
            <!-- Chart -->
            <div align="center">
              <h5 class="text-center">Scatter Plot Data (Chart.js)</h5>
              <canvas id="scatter-chartjs" height="400" width="400"></canvas>
            </div>
          </div>

        </div>

        <hr>

        <!-- Model Architecture -->
        <div>
          <ModelMaker
              :input-shape="inputShape"
              :output-shape="outputShape"
              :default-learning-rate="0.01"
              :default-epochs="50"
              :default-layers="[{type: 'dense', units: 1, activation: 'null'}]"
              :default-loss="'losses.meanSquaredError'"
              :default-optimizer="'sgd'"
              @trainModel="trainModel">
          </ModelMaker>
          <Alert :text="statusMessage"></Alert>
        </div>

        <!-- Graphs -->
        <Graph ref="graph"></Graph>

        <hr>

        <!-- Predict -->
        <div>
          <h5>Predict</h5>
          <div class="form-group row">
            <label class="col-sm-3 col-form-label">Input Value</label>
            <div class="col-sm-9">
              <input class="form-control" type="number" v-model="inputValue">
            </div>
          </div>

          <Button text="Predict" :click="predict" :disabled="!modelTrained || !inputValue"></Button>
          <Alert :text="result"></Alert>
        </div>

      </div>
    </section>
    <!-- endregion -->
  </div>
</template>

<script>
import TemplatePlayGround from '@/components/TemplatePlayGround'
import ModelMaker from '@/components/ModelMaker'
import Graph from "@/components/Graph";
import Button from '@/components/Button'
import Alert from '@/components/Alert'
import * as tf from '@tensorflow/tfjs'
import Chart from 'chart.js'

require('babel-polyfill')

export default {
  components: {
    TemplatePlayGround,
    ModelMaker,
    Graph,
    Button,
    Alert
  },
  data() {
    return {
      headerNav: 'PlayGround',
      sectionName: 'Linear Regression',
      sectionTitle: 'Linear Regression using TensorFlow.js',
      sectionDescription: `Linear regression is a statistical function that forms the basis of
      almost all machine learning algorithms. This function aims to make a straight line that
      can guess / estimate the output of an input based on the data provided. You can change
      the Model architecture and HyperParameters.`,

      x: '-1, -2, 0, 0, 1, 1, 2, 2, 3, 3, 4, 4, 4, 5, 6',
      y: '-1, -2, -1, 1, 1, 0, 2, 3, 1, 3, 2, 4, 3, 6, 5',
      label: 'toy data',

      inputShape: [1],
      outputShape: 1,
      statusMessage: null,
      model: null,
      modelInitialized: false,
      modelTrained: false,

      inputValue: null,
      result: null,
      // CHART.JS VIS
      ctx: null,
      scatterChart: null
    }
  },
  methods: {
    prepare() {
      this.ctx = document.getElementById('scatter-chartjs')
      this.scatterChart = new Chart(this.ctx, {
        type: 'bubble',
        data: {
          datasets: [{
            data: this.toyData,
            label: this.label,
            backgroundColor: 'blue'
          }]
        },
        options: {
          responsive: false
        }
      })
    },
    refreshChart() {
      this.scatterChart.destroy()
      this.scatterChart = new Chart(this.ctx, {
        type: 'bubble',
        data: {
          datasets: [{
            data: this.toyData,
            label: this.label,
            backgroundColor: 'blue'
          }]
        },
        options: {
          responsive: false
        }
      })
    },
    zip(arr1, arr2) {
      return arr1.map((x, i) => {
        return {x: x, y: arr2[i]}
      })
    },
    async trainModel(model, batchSize, epochs, modelInitialized) {
      this.statusMessage = null
      try {
        this.model = model
        this.modelInitialized = modelInitialized
        if (this.lengthOfX === this.lengthOfY) {
          this.statusMessage = 'Training, please wait...'
          // const loss = document.getElementById('loss-graph')
          await this.model.fit(this.xTrain, this.yTrain, {epochs: epochs}).then((history) => {
            const mse = this.model.evaluate(this.xTrain, this.yTrain)

            this.viewPrediction(this.model)

            this.statusMessage = 'MSE: ' + mse.dataSync()

            this.modelTrained = true

            this.$refs.graph.showGraph(history)
          })
        } else {
          this.statusMessage = 'Length of X and Y are not equal.'
        }
      } catch (err) {
        this.model = null
        this.modelInitialized = false
        this.modelTrained = false
        this.statusMessage = err.message;
        console.log(err)
      }
    },
    predict() {
      const yPred = this.model.predict(tf.tensor2d([parseFloat(this.inputValue)], [1, 1]))
      this.result = yPred.dataSync()

      this.scatterChart.data.datasets[2].data[0] = {x: parseFloat(this.inputValue), y: this.result, r: 5}
      this.scatterChart.update()
    },
    viewPrediction(model) {
      const tPred = model.predict(this.xTrain)
      const yPred = tPred.dataSync()
      const arPred = this.zip(this.xArray, yPred)

      this.scatterChart.destroy()
      this.scatterChart = new Chart(this.ctx, {
        type: 'bubble',
        data: {
          labels: this.xArray,
          datasets: [
            {
              type: 'bubble',
              label: 'training data',
              data: this.toyData,
              backgroundColor: 'red',
              borderColor: 'transparent'
            },
            {
              type: 'line',
              label: 'prediction line',
              data: arPred,
              fill: false,
              borderColor: 'blue',
              pointRadius: 0
            },
            {
              type: 'bubble',
              label: 'predict data',
              data: [],
              backgroundColor: '#32fa32',
              borderColor: 'green'
            }
          ]
        },
        options: {responsive: false}
      })
    },
  },
  computed: {
    lengthOfX() {
      return this.x.split(',').map(Number).length
    },
    lengthOfY() {
      return this.y.split(',').map(Number).length
    },
    xArray() {
      return this.x.split(',').map(Number)
    },
    yArray() {
      return this.y.split(',').map(Number)
    },
    toyData() {
      return this.zip(this.xArray, this.yArray)
    },
    xTrain() {
      return tf.tensor2d(this.xArray, [this.xArray.length, 1])
    },
    yTrain() {
      return tf.tensor2d(this.yArray, [this.yArray.length, 1])
    },
  },
  watch: {
    xArray() {
      if (this.lengthOfX !== this.lengthOfY) {
        this.statusMessage = 'Length of X and Y are not equal.'
      } else {
        this.statusMessage = null
      }
    },
    yArray() {
      if (this.lengthOfX !== this.lengthOfY) {
        this.statusMessage = 'Length of X and Y are not equal.'
      } else {
        this.statusMessage = null
      }
    },
  },
  mounted() {
    this.prepare()
  },
}
</script>

